/* https://codepen.io/chancesq/pen/bGpddXE */
#btn-login-page {
  align-items: center;
	background-image: linear-gradient(315deg, rgb(224, 43, 46) 20%, rgb(243, 114, 44) 74%);
	background-size: 200% 100%;
	background-position: left;
	background-repeat: no-repeat;
  border: none;
  color: rgba(248, 94, 0, 0.8);
	cursor: pointer;
  display: flex;
  font-family: "Open Sans","Source Sans Pro",Helvetica,sans-serif;
  font-size: 1em;
  letter-spacing: 0.3em;
  justify-content: center;
  height: 42px;
  margin-top: 60px;
  text-transform: uppercase;
  transition: 500ms;
	width: 156px;	
}

#btn-login-page:before {
  align-items: center;
  background-position: right;
	background-color: rgb(241, 194, 230);
  color: rgba(248, 94, 0, 0.8);
	content: 'Entrar';
	display: flex;
	font-size: 14px;
  justify-content: center;
  height: 36px;
	position: absolute;
  transition: 500ms;
	width: 150px;	
}

#btn-login-page:hover {
	background-position: right;
}

#btn-login-page:hover:before {
	color: rgba(248, 94, 0, 0.8);
}

#btn-login-page:focus {
	outline: none;
}

