/* referência para checkbox formato coração: https://www.youtube.com/watch?v=jQuLIUZIG3k&ab_channel=CodingArtist */
input[type="checkbox"] {
  -webkit-appearance: none;
  background-color: rgb(217, 217, 217);
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  outline: none;
  height: 12px;
  width: 12px;
  margin-left: 20px;
}

input[type="checkbox"]::before {
  position: absolute;
  content: "";
  background-color: rgb(217, 217, 217);
  height: 80%;
  width: 100%;
  top: -59%;
  border-radius: 75px 75px 0 0;
  
}

input[type="checkbox"]::after {
  position: absolute;
  content: "";
  background-color: rgb(217, 217, 217);
  height: 67%;
  width: 100%;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  top: 17%;
  right: 78%;
  border-radius: 75px 75px 0 0;
}

input[type="checkbox"]:checked, input:checked[type="checkbox"]::before, input:checked[type="checkbox"]::after {
  background-color: rgb(193, 18, 31);
}

input[type="checkbox"], input[type="checkbox"]::before, input[type="checkbox"]::after{
  margin-bottom: 18px;
}

